<template v-for="item in items">
          <v-layout
          row
          v-if="item.heading"
          align-center
          :key="item.heading"
          >
            <v-flex xs6>
                <v-subheader v-if="item.heading">
                {{ item.heading }}
                </v-subheader>
            </v-flex>
            <v-flex xs6 class="text-xs-center">
                <a href="#!" class="body-2 black--text">EDIT</a>
            </v-flex>
          </v-layout>
          <v-list-group
            v-else-if="item.children"
            v-model="item.model"
            :key="item.text"            
            :append-icon="item.model ? item.icon : item['icon-alt']"
            prepend-icon=""
            >
            <v-list-tile 
              slot="activator"
              >
              <v-list-tile-action>
                <v-icon>{{ item.vicon }}</v-icon>
              </v-list-tile-action>
                <v-list-tile-content>
                  <v-list-tile-title>
                      {{ item.text }}
                  </v-list-tile-title>
                </v-list-tile-content>
            </v-list-tile>
            <v-list-tile
                v-for="(child, i) in item.children"
                :key="i"
                :color="active ? 'primary' : ''"
                @click=""
            >
              <v-list-tile-content>
                <v-list-tile-title class="text-xs-left">
                    {{ child.text }}
                </v-list-tile-title>
              </v-list-tile-content>
              <v-list-tile-action v-if="child.icon">
                <v-icon>{{ child.icon }}</v-icon>
              </v-list-tile-action>                
            </v-list-tile>
          </v-list-group>
          <v-list-tile v-else @click="" :key="item.text">
            <v-list-tile-action>
                <v-icon>{{ item.icon }}</v-icon>
            </v-list-tile-action>
            <v-list-tile-content>
                <v-list-tile-title>
                {{ item.text }}
                </v-list-tile-title>
            </v-list-tile-content>
          </v-list-tile>
        </template>

<script>
export default {
    data: () => ({
        items: [
        { icon: 'dashboard', text: 'Главная' },
        {
          vicon: 'people', text: 'Админ(центр.)',
          icon: 'arrow_drop_up', 'icon-alt': 'arrow_drop_down',          
          model: false,
          children: [
          { text: 'ЦВШ',icon: 'arrow_right' },
          { text: 'Области'},
          { text: 'ОВК' },
          { text: 'ДВК' }
          ]
        },        
        { divider: true },
        { icon: 'person', text: 'Кандидаты' },
        { icon: 'insert_chart_outlined', text: 'Статистика' },
        { icon: 'assignment', text: 'Соц.опрос' },
        { icon: 'not_listed_location', text: 'Вопросы' },
        { icon: 'account_box', text: 'Активисты' }
      ],     
      pagesname: [
        {
          text: 'Главная',
          disabled: false,
          href: 'breadcrumbs_home'
        },
        {
          text: 'Статистика',
          disabled: true,
          href: 'breadcrumbs_link_1'
        }
      ],
      dropdown_lang: [
        {text: 'УКР'},
        {text: 'РУС'}
      ]      
    })
}
</script>

